























.screenshots {
    @apply tw-overflow-auto tw-flex tw--mx-4;
    -webkit-overflow-scrolling: touch;

    .screenshot {
        @apply tw-px-4 tw-flex-shrink-0 tw-flex-grow-0;
        flex-basis: 90%;

        img {
            @apply tw-rounded;
        }
    }
}

@media (min-width: 672px) {
    .screenshots {
        .screenshot {
            flex-basis: 45%;
        }
    }
}

@media only screen and (min-width: 1400px) {
    .screenshots {
        .screenshot {
            flex-basis: 31%;
        }
    }
}
