

































.cms-editions {
    @apply tw-py-6;

    .cms-editions-edition {
        &:not(:last-child) {
            @apply tw-mb-6;
        }
    }
}

@media (min-width: 992px) {
    .cms-editions {
        @apply tw-flex tw--mx-4 tw-justify-center;

        .cms-editions-edition {
            @apply tw-w-1/3 tw-mx-4;

            &:not(:last-child) {
                @apply tw-mb-0;
            }
        }
    }
}
