




















.status-message {
    @apply tw-flex-1 tw-flex tw-items-center tw-justify-center tw-text-center;

    div {
        .c-spinner {
            @apply tw-mb-6;
        }

        .c-icon {
            @apply tw-mb-6;

            width: 48px;
            height: 48px;
        }

        .message {
            @apply tw-text-center;
        }
    }
}
