



































































































































































































































































































@import "@craftcms/sass/mixins";

table.cart-data {
    border-top: 1px solid #eee;

    thead,
    tbody {
        border-bottom: 1px solid #eee;
    }

    tr {
        th, td {
            padding: 7px 0;
        }

        td.expiry-date {
            & > div {
                display: inline-block;
                margin-bottom: 0;
            }

            .c-spinner {
                @apply tw-relative tw-ml-4;
                top: 6px;
            }
        }

        td.thin {
            .c-btn {
                white-space: nowrap;
            }
        }
    }

    .item-name {
        .edition-badge {
            @apply tw-ml-2;
        }
    }

    .plugin-icon {
        margin-right: 10px !important;

        img {
            max-width: none;
        }
    }
}

@media (max-width: 991px) {
    table.cart-data {
        thead {
            display: none;
        }


        tr,
        td,
        th {
            display: block;
        }

        tr {
            &.sub-item {
                td.blank-cell,
                td.empty-cell {
                    display: none;
                }
            }
        }
    }
}

@media (min-width: 992px) {
    table.cart-data {
        tr {
            &.sub-item {
                td:not(.blank-cell) {
                    border-top: 1px dotted #eee;
                }
            }

            th,
            td {
                padding: 10px 0;

                &.price {
                    text-align: right;
                }

                &.total-price {
                    text-align: right;
                }
            }

            td.expiry-date {
                @apply tw-w-3/5;
            }
        }
    }
}
