





















































































.cms-editions-edition {
    @apply tw-border tw-border-gray-200 tw-border-solid tw-p-8 tw-rounded tw-text-center tw-flex tw-flex-col;

    .description {
        @apply tw-flex-1;

        .edition-name {
            @apply tw-border-b tw-border-gray-200 tw-border-solid tw-text-gray-600 tw-inline-block tw-py-1 tw-uppercase tw-text-lg tw-font-bold;
        }

        .edition-description {
            @apply tw-text-lg tw-my-6 tw-leading-normal;
        }

        .price {
            @apply tw-text-3xl tw-font-bold tw-my-8;
        }

        ul {
            @apply tw-text-left tw-mb-8;

            li {
                @apply tw-flex tw-items-center tw-py-2 tw-border-b tw-border-gray-300 tw-border-solid;

                &:first-child {
                    @apply tw-border-t;
                }
            }
        }
    }

    .cms-edition-actions {
        position: relative;

        .c-spinner {
            position: absolute;
            bottom: -30px;
            left: 50%;
            margin-left: -11px;
        }

        .c-btn {
            @apply tw-mt-3;
        }
    }
}
