
























































































































@import "@craftcms/sass/mixins";

.plugin-details-header {
    @apply tw-leading-normal tw-overflow-hidden tw-mb-1;
    max-height: 4.75em;

    .plugin-name {
        @apply tw-flex tw-mb-1;

        .edition-badge {
            @apply tw-ml-2;
        }
    }
}

.plugin-card {
    box-sizing: border-box;

    &:hover {
        strong {
            color: $linkColor;
        }
    }

    .installed {
        @apply tw-absolute;
        top: 14px;
        @include right(18px);
        color: #ccc;
    }
}

.ps-grid-plugins {
    .plugin-card {
        @apply tw-h-full;
    }
}
